import CanvasHelper from 'src/services/wasm/canvasHelper';
import { appSettings } from 'src/services/settings';
import { IPointer } from 'src/services/wasm-worker/wasmTypes';
import { HEAP_TYPE_ENUM, WasmCropTypes } from 'src/services/wasm/CONSTANTS';
import { allocateMemory, freeMemory } from 'src/services/wasm-worker/memoryHelper';
import wasm from './wasm';

class CaptureProcessWorker {
  // allocated memory to run per frame without reallocation memory each time
  private cImageStructPointer: IPointer;
  private imageDataPointer: IPointer;

  public start(width: number, height: number, cropType?: WasmCropTypes) {
    this.cImageStructPointer = allocateMemory(CanvasHelper.getcImageStruct(width, height));

    wasm.startImageDetection(appSettings.capture.minFramesCount, appSettings.capture.minCaptureTime, cropType);
  }

  public captureNextFrame = (bmpData: Uint8ClampedArray) => {
    if (!this.imageDataPointer) {
      this.imageDataPointer = allocateMemory(bmpData, HEAP_TYPE_ENUM.HEAP8);
    }

    this.imageDataPointer.setData(bmpData);

    return wasm.addImage(this.imageDataPointer.ptr, this.cImageStructPointer.ptr);
  };

  public stop = () => {
    wasm.stopImageDetection();
    freeMemory(this.cImageStructPointer);
    freeMemory(this.imageDataPointer);

    this.cImageStructPointer = null;
    this.imageDataPointer = null;
  };
}

export default new CaptureProcessWorker();
