import { IWASMModule } from 'src/services/wasm-worker/wasmTypes';
import { AnyAction } from 'redux';
import registerPromiseWorker from 'promise-worker-transferable/register';
import WASM_ACTION_TYPES from 'src/services/wasm-worker/WASM_ACTION_TYPES';
import wasm from './wasm';
import captureProcessWorker from 'src/services/wasm-worker/captureProcessWorker';

declare var Module: IWASMModule;

if (typeof window === 'undefined') {
  (self as any).window = self; // webpack hot-reload bug
}

const actions: { [key in WASM_ACTION_TYPES]?: (action: AnyAction, withTransferList?: any) => Promise<AnyAction> } = {};

registerPromiseWorker(async (message, withTransferList) => {
  if (typeof message === 'string') {
    return {
      type: WASM_ACTION_TYPES.OK,
    };
  }

  const actionType = message.type as WASM_ACTION_TYPES;

  if (actions[actionType]) {
    try {
      return await actions[actionType](message, withTransferList);
    } catch (e) {
      window.console.error('Error performing wasm action: ' + actionType);
      window.console.error(e);
    }
  } else {
    throw new Error(`Wasm action ${actionType} not supported`);
  }
});

actions[WASM_ACTION_TYPES.LOAD_WASM] = async (data) => {
  importScripts('/' + data.path + '/wasm_engine.js');

  // TODO wait for wasm initialization
  await new Promise((resolve) => {
    const checkInit = () => {
      if ((Module as any).asm.vc) {
        resolve();
      } else {
        setTimeout(checkInit, 100);
      }
    };

    checkInit();
  });

  return {
    type: WASM_ACTION_TYPES.OK,
  };
};

actions[WASM_ACTION_TYPES.CHECK_LICENSE] = async (data) => {
  await wasm.checkLicense(data.licenseFilePath);

  return {
    type: WASM_ACTION_TYPES.OK,
  };
};

actions[WASM_ACTION_TYPES.DETECT_EDGES_AND_CROP] = async (data, withTransferList) => {
  const { bmpData, imageWidth, imageHeight, imageQuality } = data;
  const result = wasm.detectEdgesAndCrop(bmpData, imageWidth, imageHeight, imageQuality);

  // TODO is result.data.buffer memory free after this?
  return withTransferList(
    {
      result,
      type: WASM_ACTION_TYPES.OK,
    },
    [result.data.buffer]
  );
};

actions[WASM_ACTION_TYPES.CHECK_QUALITY_OCR] = async (data) => {
  const { jpegData, documentBoundary } = data;

  return {
    result: wasm.checkQualityAssessmentForOCR(jpegData, documentBoundary),
    type: WASM_ACTION_TYPES.OK,
  };
};

actions[WASM_ACTION_TYPES.RECROP_IMAGE] = async (data, withTransferList) => {
  const { jpegData, documentBoundary, imageQuality } = data;
  const result = wasm.recropImage(jpegData, documentBoundary, imageQuality);

  return withTransferList(
    {
      result,
      type: WASM_ACTION_TYPES.OK,
    },
    [result.buffer]
  );
};

actions[WASM_ACTION_TYPES.START_IMAGE_DETECTION] = async (data) => {
  await captureProcessWorker.start(data.width, data.height, data.cropType);

  return {
    type: WASM_ACTION_TYPES.OK,
  };
};

actions[WASM_ACTION_TYPES.CAPTURE_NEXT_FRAME] = async (data) => {
  const result = await captureProcessWorker.captureNextFrame(data.bmpData);

  return {
    result,
    type: WASM_ACTION_TYPES.OK,
  };
};

actions[WASM_ACTION_TYPES.STOP_IMAGE_DETECTION] = async () => {
  captureProcessWorker.stop();

  return {
    type: WASM_ACTION_TYPES.OK,
  };
};
