declare var Module: IWASMModule;

import { allocateMemory, freeMemory } from 'src/services/wasm-worker/memoryHelper';
import { HEAP_TYPE_ENUM, TErrorCode, TErrorCodeDescription, TPixelFormat, WasmCropTypes } from '../wasm/CONSTANTS';
import { IFrameReport, IPointer, IWasmImageResult, IWASMModule } from 'src/services/wasm-worker/wasmTypes';
import { IPoint, SuitabilityForOcrVerdict, TQualityAssessmentResult } from 'src/types';
import { arrayToPoints, pointsToArray } from 'src/services/wasm/utils';
import { TJpegResult } from 'src/types/declaration';
import CanvasHelper from 'src/services/wasm/canvasHelper';

class Wasm {
  private frameReport: IPointer;
  private status: IPointer;

  /**
   * Determine crop boundaries and crop image, result in jpeg. Usage: get original image in jpeg and crop image from gallery/device camera1
   * @param {Uint8ClampedArray} jpegData - image data, bmp
   * @param {number} imageWidth - image width
   * @param {number} imageHeight - image height
   * @param {boolean} isSecured - get secure image
   * @param {number} imageQuality - result jpeg quality, 0-100%
   * @param {boolean} noCrop - If true - not crop, resulted image just normalized
   */
  public detectEdgesAndCrop = (
    bmpData: Uint8ClampedArray,
    imageWidth: number,
    imageHeight: number,
    imageQuality: number
  ) => {
    this.setConfig(0, 1);

    let result: TJpegResult = null;

    const cImgPointer = allocateMemory(CanvasHelper.getcImageStruct(imageWidth, imageHeight));
    const imagePointer = allocateMemory(bmpData, HEAP_TYPE_ENUM.HEAP8);
    // corner points  data

    // tslint:disable-next-line:no-magic-numbers
    const pointsPointer = allocateMemory(new Int32Array(8));
    const isEdgesDetectedPointer = allocateMemory(new Int32Array(1));

    let errorCode = Module._WasmDetectEdges(
      cImgPointer.ptr,
      imagePointer.ptr,
      TPixelFormat.PF_RGBA8888,
      // tslint:disable-next-line:no-magic-numbers
      pointsPointer.ptr,
      isEdgesDetectedPointer.ptr
    );

    if (errorCode === TErrorCode.NoError) {
      // no edges detected
      if (isEdgesDetectedPointer.getData()[0] === 0) {
        pointsPointer.setData(new Int32Array([0, 0, 0, 0, 0, 0, 0, 0]));
      }

      const resultedPointerSize = allocateMemory(new Int32Array(1));

      // override bmp imagePointer with jpeg result
      errorCode = Module._WasmGetCroppedJpegImage(
        cImgPointer.ptr,
        imagePointer.ptr,
        TPixelFormat.PF_RGBA8888,
        pointsPointer.ptr,
        1,
        resultedPointerSize.ptr,
        imagePointer.ptr,
        imagePointer.size,
        imageQuality,
        0
      );

      if (errorCode === TErrorCode.NoError) {
        const croppedImageData = imagePointer.getData(resultedPointerSize.getData()[0]);

        result = {
          size: croppedImageData.length,
          data: croppedImageData,
          edges: arrayToPoints(pointsPointer.getData()),
        };
      }

      freeMemory(resultedPointerSize);
    }

    freeMemory(cImgPointer);
    freeMemory(imagePointer);
    freeMemory(pointsPointer);
    freeMemory(isEdgesDetectedPointer);

    if (errorCode) {
      throw new Error('Error detectEdgesAndCrop: ' + TErrorCode[errorCode]);
    }

    return result;
  };

  /**
   * check if image ready for recognition
   * @param {Uint8ClampedArray} jpegData - image data jpeg
   * @param {IPoint[]} vertices - crop vertices
   * @returns { TQualityAssessmentResult } - wasm estimator result
   */
  public checkQualityAssessmentForOCR(jpegData: Uint8ClampedArray, vertices: IPoint[]): TQualityAssessmentResult {
    let result: TQualityAssessmentResult = null;

    const bufferPointer = allocateMemory(jpegData, HEAP_TYPE_ENUM.HEAP8);
    const pointsPointer = allocateMemory(pointsToArray(vertices));
    const verdictPointer = allocateMemory(new Uint32Array(1));
    const confidencePointer = allocateMemory(new Float64Array(1), HEAP_TYPE_ENUM.HEAP64);

    const errorCode = Module._WasmIsSuitableForOcrJpeg(
      bufferPointer.ptr,
      jpegData.length,
      pointsPointer.ptr,
      1,
      verdictPointer.ptr,
      confidencePointer.ptr
    );

    if (!errorCode) {
      const verdict: SuitabilityForOcrVerdict = verdictPointer.getData()[0];
      const confidence: number = confidencePointer.getData()[0];

      result = {
        verdict,
        confidence,
      };
    }

    freeMemory(bufferPointer);
    freeMemory(pointsPointer);
    freeMemory(verdictPointer);
    freeMemory(confidencePointer);

    if (errorCode) {
      throw new Error('Error checkQualityAssessmentForOCR: ' + TErrorCode[errorCode]);
    }

    return result;
  }

  /**
   * recrop image by new boundaries
   * @param {Uint8ClampedArray} jpegData - image data
   * @param {IPoint[]} documentBoundary - crop coords
   * @param {number} imageQuality - jpeg quality, 0-100
   */
  public recropImage(
    jpegData: Uint8ClampedArray,
    documentBoundary: Int32Array,
    imageQuality: number
  ): Uint8ClampedArray {
    // source image data
    const sourcePointer = allocateMemory(jpegData, HEAP_TYPE_ENUM.HEAP8);
    // corner points  data
    const pointsPointer = allocateMemory(documentBoundary);
    // size of resulted image would be placed here
    const resultedBufferSizePointer = allocateMemory(new Uint32Array(1));
    // in case if resulted jpeg would be larger, than source we multiply allocated buffer by 2
    const targetBufferSize = jpegData.length * 2;
    // resulted jpg will be placed here
    const targetBufferPointer = allocateMemory(new Uint8ClampedArray(targetBufferSize), HEAP_TYPE_ENUM.HEAP8);

    const errorCode = Module._WasmRecropJpegImage(
      sourcePointer.ptr,
      sourcePointer.size,
      pointsPointer.ptr,
      1,
      resultedBufferSizePointer.ptr, // TODO remove this from API
      targetBufferPointer.ptr,
      targetBufferSize,
      imageQuality,
      false
    );

    const size = resultedBufferSizePointer.getData()[0];
    const result = targetBufferPointer.getData(size);

    freeMemory(sourcePointer);
    freeMemory(pointsPointer);
    freeMemory(resultedBufferSizePointer);
    freeMemory(targetBufferPointer);

    if (errorCode) {
      throw new Error('Error recropImage: ' + TErrorCode[errorCode]);
    }

    return result;
  }

  public checkLicense = async (licenseFile: string) => {
    const buffer = await fetch(licenseFile).then((response) => response.arrayBuffer());

    const data = allocateMemory(new Uint8Array(buffer), HEAP_TYPE_ENUM.HEAP8);
    const errorCode = Module._WasmSetLicense(data.ptr, data.size);

    freeMemory(data);

    if (errorCode) {
      throw new Error('License Error:' + TErrorCodeDescription[errorCode]);
    }
  };

  /**
   * Run new session, looking for stable image
   * @param {Number} minFramesCount - min number of stable frames to wait before decision than best frame found.
   * must be >= 2
   * @param cropType
   */
  public startImageDetection(minFramesCount: number, stabilizationTime: number, cropType = WasmCropTypes.Fast): void {
    this.setConfig(0, cropType);

    // Second parameter - edges detection quality ( -1 choose automatically )
    // Third parameter - inertial detection of edges. Value - number of skipped frames ( detected based on motion detection )
    const errorCode = Module._WasmStartImageDetection(minFramesCount, stabilizationTime, 0, 0);

    if (errorCode) {
      throw new Error('Error startImageDetection: ' + TErrorCode[errorCode]);
    }

    if (!this.frameReport) {
      this.frameReport = allocateMemory(new Int32Array(10));
    }
    if (!this.status) {
      this.status = allocateMemory(new Int32Array(1));
    }
  }

  /**
   * Stop image detection session
   */
  public stopImageDetection(): void {
    const errorCode = Module._WasmStopImageDetection();

    freeMemory(this.frameReport);
    freeMemory(this.status);

    if (errorCode) {
      throw new Error('Error stopImageDetection: ' + TErrorCode[errorCode]);
    }

    this.frameReport = null;
    this.status = null;
  }

  /**
   * add Image to current session
   * @returns {Object} IWasmImageResult
   */
  public addImage(imageDataPointerPtr: number, cImageStructPointerPtr: number): IWasmImageResult {
    const errorCode = Module._WasmAddImage(
      cImageStructPointerPtr,
      imageDataPointerPtr,
      TPixelFormat.PF_RGBA8888,
      this.frameReport.ptr,
      this.status.ptr
    );

    if (errorCode) {
      throw new Error('Error stopImageDetection: ' + TErrorCode[errorCode]);
    }

    return {
      ...this._getFrameReport(),
      status: this.status.getData()[0],
    };
  }

  private _getFrameReport(): IFrameReport {
    const frameReport: Int32Array = this.frameReport.getData();

    // tslint:disable:no-magic-numbers
    return {
      quality: frameReport[0],
      vertices: frameReport.slice(1, 9),
      isNewBest: !!frameReport[9],
    };
  }

  /**
   *
   * @param param, currently only 0 = selected crop
   * @param value, for 0(selected crop ) = 0,1,2 - compact, quality, fast
   */
  private setConfig(param: number, value: number) {
    const valuePtr = allocateMemory(new Int32Array([value]));

    Module._WasmConfig(param, valuePtr.ptr);

    freeMemory(valuePtr);
  }
}

export default new Wasm();
